<template>
  <div class="my">
    <div class="my-txt clearfix">
      <div class="my-me">个人信息</div>
      <div class="fr my-address" v-if="ifbj">
        <div class="hui">
          <div>背景编辑</div>
          <van-uploader class="upload-box" :max-size="maxSize" :after-read="reads" />
        </div>
      </div>
    </div>
    <div class="portrait" :style="{backgroundImage: 'url(' + information.userBg + ')'}">
      <div class="head">
        <img class="auto-img" :src="information.userImg" alt />
      </div>
      <div class="test" v-if="ifbj">
        <div class="prop-name">{{information.nickName}}</div>
        <div class="prop-brief">简介：{{information.desc == '' ? '这人太懒了，什么也没留下!' : information.desc}}</div>
      </div>
      <div class="loginelse" v-else @click="gologin">登录</div>
    </div>
    <van-cell-group>
      <van-cell @click="goaddress(item.routeName)" v-for="(item,index) in group" :key="index" :title="item.title" is-link />
    </van-cell-group>
  </div>
</template>

<script>
import "../../assets/less/my.less";
export default {
  data() {
    return {
      group: [
        { title: "个人资料",routeName: 'Account'},
        { title: "安全中心",routeName: 'Secure'},
        { title: "我的收藏",routeName: 'Like'},
        { title: "我的地址",routeName: 'MyAddressBar'}
      ],
      information: {},
      ifbj: false,
      maxSize: 1 * 1024 * 1024
    };
  },
  created() {
    this.userId();
    if (localStorage.getItem("_Tok")) {
      this.ifbj = true;
    }
  },
  methods: {
    gologin(){
      this.$router.push({name : "Login"})
    },
    goaddress(name) {
      if (name === 'Login') {
        this.$router.push({name});
      } else {
        if (!this.ifbj) {
          this.$toast('请先登录');
        } else {
          this.$router.push({name});
        }
      }
      
    },
    userId() {
      let tokenString = localStorage.getItem("_Tok");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          if (result.data.code == "A001") {
            this.information = result.data.result[0];
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    reads(imge) {

      let serverBase64Img = imge.content.replace(/^data:image\/[A-Za-z]+;base64,/, '');

      let imgType = imge.file.type.split('/')[1];


      let tokenString = localStorage.getItem("_Tok");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img,
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 'I001') {
            this.userInfo.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
  }
};
</script>